import React, { useState, useEffect } from 'react'
import { Row, Col } from "reactstrap";
import { Toolbar, Typography, IconButton } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import { Calendar, Views, momentLocalizer } from 'react-big-calendar'
import moment from 'moment-timezone'
import NoImage from '../../../assets/image/NoImage'
import "react-big-calendar/lib/css/react-big-calendar.css";
import "react-big-calendar/lib/addons/dragAndDrop/styles.css";
import ModalFormJanji from './Modal.FormJanji'
import mBuatJanji from '../../../Controler/c_BuatJanji'



const localizer = momentLocalizer(moment);
const Modal = (props) => {
    const authLogin = props.authLogin
    const dokter = props.dokter || {}
    const tanggal = props.tanggal
    const [isReload, setIsReload] = useState(true)
    const [hoursMin, minutesMin] = dokter.jadwalNow.jamMulai.split(':')
    const minTime = new Date(tanggal);
    minTime.setHours(hoursMin, minutesMin, 0);
    const [hoursMax, minutesMax] = dokter.jadwalNow.jamSelesai.split(':')
    const maxTime = new Date(tanggal);
    maxTime.setHours(hoursMax, minutesMax - 1, 0);
    const [isOpenForm, setIsOpenForm] = useState(false)
    const [dateSelect, setDateSelect] = useState({})
    const [listJanji, setListJanji] = useState([])

    useEffect(() => {
        mBuatJanji.listJanji(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, dokter.username, moment(tanggal).format("YYYY-MM-DD")).then(resp => {
            setListJanji(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false))
        return () => {

        }
    }, [isReload, authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, dokter.username, tanggal])

   
    const handleClose = () => {
        props.setOpen(false)
    }
    const pilihJam = ({ start, end }) => {
        setDateSelect({ start, end })
        setIsOpenForm(true)
    }
    const toolbar = () => (
        <div>
            <h4 className="text-center"><b>Pilih Jam Janji</b></h4>
        </div>
    )
    return (
        <Dialog fullScreen open={props.open} onClose={handleClose} TransitionComponent={Transition}>
            <AppBar>
                <Toolbar>
                    <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
                        <CloseIcon />
                    </IconButton>
                    <Typography variant="h6">
                        Buat Janji
                    </Typography>
                </Toolbar>
            </AppBar>

            <div className="contentManual bodyDialog">
                <Row style={{ marginTop: 20, marginBottom: 20 }}>
                    <Col xs={12} md={3}>
                        <img className="imgDokter mx-auto d-block" alt="Dokter" src={dokter.gambar || `data:image/jpeg;base64,${NoImage}`} />
                        <span className="title-dokter d-block">{dokter.nama}</span><br />
                        <span className="jadwal-dokter d-block"><b>{moment(tanggal).format("DD-MMM-YYYY")}</b></span><br />
                        <span className="jadwal-dokter d-block">{dokter.jadwalNow.jamMulai} - {dokter.jadwalNow.jamSelesai}</span>
                    </Col>
                    <Col xs={12} md={9}>
                        <Calendar
                            selectable
                            events={listJanji}
                            defaultView={Views.DAY}
                            views={['day']}
                            step={30}
                            showMultiDayTimes
                            date={moment(tanggal)}
                            min={minTime}
                            max={maxTime}
                            localizer={localizer}
                            formats={{
                                timeGutterFormat: 'HH:mm',
                                eventTimeRangeFormat: ({
                                    start,
                                    end
                                }, culture, local) =>
                                    local.format(start, 'HH:mm', culture) + ' - ' +
                                    local.format(end, 'HH:mm', culture)
                            }}
                            onSelectSlot={pilihJam}
                            components={{
                                toolbar: toolbar
                            }}
                        />

                    </Col>
                </Row>
            </div>
            {isOpenForm && <ModalFormJanji open={isOpenForm} setOpen={setIsOpenForm} authLogin={authLogin} dateSelect={dateSelect} dokter={dokter} tanggal={tanggal} setIsReload={setIsReload} setLoadJanji={props.setLoadJanji}/>}
        </Dialog>
    )
}

export default Modal;
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});