import React, { useState, useEffect } from "react";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import moment from 'moment-timezone'
import mBuatJanji from '../../../Controler/c_BuatJanji'
import Notifikasi from '../../../helpers/Notifikasi'
import { isArray } from "util";


const Modal = (props) => {
    const authLogin = props.authLogin
    const dariJam = props.dateSelect.start
    const sampaiJam = props.dateSelect.end
    const [keperluan, setKeperluan] = useState('')
    const [isAlert, setIsAlert] = useState(false)
    const [alertText, setAlertText] = useState("")

    const handleClose = () => {
        props.setOpen(false);
    };
    const simpan = () => {
        if (keperluan.length <= 0) {
            setAlertText("Wajib isi keperluan !!!")
            setIsAlert(true)
        } else {
            mBuatJanji.buatJanji(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, authLogin.username, props.dokter.username, moment(props.tanggal).format("YYYY-MM-DD"), dariJam, sampaiJam, keperluan).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil buat janji", "")
                    props.setIsReload(true)
                    props.setLoadJanji(true)
                    handleClose()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }
    }
    useEffect(() => {
        const timer = setTimeout(() => {
            setIsAlert(false)
        }, 3000);
        return () => clearTimeout(timer);
    }, [isAlert]);

    return (
        <Dialog open={props.open} onClose={handleClose} aria-labelledby="form-dialog-title">
            <DialogTitle id="form-dialog-title">Form Buat Janji</DialogTitle>
            {isAlert && (
                <DialogContentText>
                    <Alert severity="error" onClose={() => { setIsAlert(false) }}>{alertText}</Alert>
                </DialogContentText>
            )}
            <DialogContent>
                <TextField
                    margin="dense"
                    id="dokter"
                    label="Pasien"
                    fullWidth
                    multiline={true}
                    value={authLogin.username}
                    disabled />
                <TextField
                    margin="dense"
                    id="dokter"
                    label="Nama Dokter"
                    fullWidth
                    multiline={true}
                    value={props.dokter.nama}
                    disabled />
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <DatePicker
                        fullWidth={true}
                        label="Tanggal"
                        format="dd-MM-yyyy"
                        value={moment(props.tanggal)}
                        KeyboardButtonProps={{
                            'aria-label': 'change date',
                        }}
                        ampm={false}
                        disabled
                    />
                </MuiPickersUtilsProvider>
                <TextField
                    margin="dense"
                    id="darijam"
                    label="Dari Jam"
                    fullWidth
                    multiline={true}
                    value={moment(dariJam).format("HH:mm")}
                    disabled />
                <TextField
                    margin="dense"
                    id="darijam"
                    label="Sampai Jam"
                    fullWidth
                    multiline={true}
                    value={moment(sampaiJam).format("HH:mm")}
                    disabled />
                <TextField
                    autoFocus
                    margin="dense"
                    id="keperluan"
                    label="Keperluan"
                    fullWidth
                    rows={2}
                    multiline={true}
                    required
                    value={keperluan}
                    onChange={(e) => setKeperluan(e.target.value)}
                />
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="secondary">
                    Cancel
          </Button>
                <Button onClick={simpan} color="primary">
                    Oke
          </Button>
            </DialogActions>
        </Dialog>
    )
}
export default Modal;
