import React, { Fragment, useState, useEffect } from "react";
import { Row, Col } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import NoImage from '../../../assets/image/NoImage'
import mBuatJanji from '../../../Controler/c_BuatJanji'
import moment from 'moment-timezone'
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import Skeleton from 'react-loading-skeleton';
import hari from '../../../data/hariDefault'
import ModalListJam from './Modal.Jam.BuatJanji'
import ModalbatalJanji from '../transaksi/konfirmasiJadwal/Modal.batalJanji'

const BuatJanji = props => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [isReload, setIsReload] = useState(true)
    const [listDokter, setListDokter] = useState([])
    const [tanggal, setTanggal] = useState(moment())
    const [dokter, setDokter] = useState({})
    const [isOpenListJam, setIsOpenListJam] = useState(false)
    const [janji, setJanji] = useState({})
    const [loadJanji, setLoadJanji] = useState(true)
    const [isModalBatal, setIsModalBatal] = useState(false)


    useEffect(() => {
        setIsReload(true)
        setListDokter([])
        mBuatJanji.listDokterReady(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, hari[moment(tanggal).format("d")]).then(resp => {
            setListDokter(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => { };
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.klinik.kodeklinik, tanggal]);

    useEffect(() => {
        mBuatJanji.cekJanji(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik,authLogin.username).then(resp => {
            setJanji(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setLoadJanji(false))
        return () => {
            
        }
    }, [authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, authLogin.username, loadJanji])
    const loadingRow = [0, 1, 2, 3];
    const openListJam = data => {
        setDokter(data)
        setIsOpenListJam(true)
    }
    const batalkanJanji = () =>{
        setIsModalBatal(true)
    }
    return (
        <Fragment>
            <Row>
                <Colxx xxs="12">
                    <Breadcrumb heading="menu.buat-janji" match={props.match} />
                    <Separator className="mb-5" />
                </Colxx>
            </Row>
            <Row>
                {!janji.kodetrans && (
                    <Col xs={12}>
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <DatePicker
                                fullWidth={true}
                                label="Pilih Tanggal"
                                format="dd-MM-yyyy"
                                value={tanggal}
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                                onChange={(d) => setTanggal(d)}
                                ampm={false}
                                disablePast
                            />
                        </MuiPickersUtilsProvider>
                    </Col>
                )}
                {isReload && loadingRow.map(el => (
                    <Col xs={12} sm={3} key={el.toString()}>
                        <Card className="tangan mgTop10">
                            <CardContent>
                                <Skeleton height={200} />
                                <span className="title-dokter d-block"><Skeleton /></span><br />
                                <span className="jadwal-dokter d-block"><Skeleton /></span>
                            </CardContent>
                        </Card>
                    </Col>
                ))}

                {janji.kodetrans ? (
                    <React.Fragment>
                        <Col xs={12} >
                            <h4 className="text-center2" style={{ marginTop: 20 }}> Anda sudah memiliki janji dengan Dokter pada tanggal <b><i>{moment(janji.janji.tanggalJanji).format("DD-MM-YYYY")}</i></b> dari jam <b><i>{janji.janji.jamMulaiJanji}</i></b> sampai <b><i>{janji.janji.jamSelesaiJanji}</i></b> </h4>
                            <h4 className="text-center2"><span className="tangan linkWarning" onClick={batalkanJanji}><b>BATALKAN</b></span> janji ini untuk buat janji baru</h4>
                        </Col>
                    </React.Fragment>
                ) : (
                        <React.Fragment>
                            {(!isReload && listDokter.length === 0) && (
                                <Col xs={12} style={{ marginTop: 30 }}>
                                    <h4 className="text-center2"><b>Tidak ada jadwal dokter praktet hari ini</b></h4>
                                </Col>
                            )}
                            {listDokter.map((el, i) => {
                                return (
                                    <Col xs={12} sm={3} key={i}>
                                        <Card className="tangan mgTop10" onClick={openListJam.bind(this, el)}>
                                            <CardContent>
                                                <img className="imgDokter mx-auto d-block" alt="Dokter" src={`data:image/jpeg;base64,${NoImage}`} />
                                                <span className="title-dokter d-block">{el.nama}</span><br />
                                                <span className="jadwal-dokter d-block">{el.jadwalNow.jamMulai} - {el.jadwalNow.jamSelesai}</span>
                                            </CardContent>
                                        </Card>
                                    </Col>
                                )
                            })}
                        </React.Fragment>
                    )}
            </Row>
            {isOpenListJam && (
                <ModalListJam open={openListJam} setOpen={setIsOpenListJam} authLogin={authLogin} dokter={dokter} tanggal={tanggal} setLoadJanji={setLoadJanji}/>
            )}
            {isModalBatal && <ModalbatalJanji open={isModalBatal} setOpen={setIsModalBatal} janji={janji} authLogin={authLogin} setIsReload={setLoadJanji}/>}
        </Fragment>
    )
}
export default BuatJanji
