import GQL from '../helpers/GQL'
import moment from 'moment-timezone'


const BuatJanji = {
	listDokterReady: (clientID, token, kodeklinik, hari) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listJadwalDokter(kodeklinik:"${kodeklinik}",hari:"${hari}"){
						nama
						username
                        jadwalNow{
                            hari
                            jamMulai
                            jamSelesai
                        }
                  }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.listJadwalDokter) return (resolve(resp.data.listJadwalDokter));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	buatJanji: (clientID, token, kodeklinik, usernamepasien, namadokter, tanggal, dariJam, sampaiJam, keperluan) => {
		return new Promise((resolve, reject) => {
			const query = `
				mutation {
                    buatJanji(kodeklinik: "${kodeklinik}", input:{kodedokter: "${namadokter}",pasien:"${usernamepasien}", keperluan:"${keperluan}",tanggalJanji:"${moment(tanggal).format("YYYY-MM-DD")}",jamMulaiJanji:"${moment(dariJam).format("HH:mm")}", jamSelesaiJanji:"${moment(sampaiJam).format("HH:mm")}" })
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.buatJanji) return (resolve(resp.data.buatJanji));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	listJanji: (clientID, token, kodeklinik, dokter, tanggal) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listJanjibyDokter(dokter:"${dokter}", kodeklinik:"${kodeklinik}", tanggal:"${tanggal}"){
						_id
						kodetrans
						janji{
						  tanggalJanji
						  jamMulaiJanji
						  jamSelesaiJanji
						}
						keperluanPasien
					  }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				const listJanji = []
				resp.data.listJanjibyDokter.map((el, i) => {
					const [hoursMin, minutesMin] = el.janji.jamMulaiJanji.split(':')
					const [hoursMax, minutesMax] = el.janji.jamSelesaiJanji.split(':')
					return listJanji.push({
						id: i,
						title: "Sudah Terisi",
						start:  new Date(moment(el.janji.tanggalJanji).tz("Asia/Jakarta").set({ hour: hoursMin, minute: minutesMin, second: 0 })),
						end:  new Date(moment(el.janji.tanggalJanji).tz("Asia/Jakarta").set({ hour: hoursMax, minute: minutesMax, second: 0 })),
					})
				})
				if (resp.data && resp.data.listJanjibyDokter) return (resolve(listJanji));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	cekJanji: (clientID, token, kodeklinik, pasien) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    JanjibyPasien(kodeklinik:"${kodeklinik}",pasien:"${pasien}"){
						_id
						kodetrans
						pasien
						keperluanPasien
						kodedokter
						janji{
						  tanggalJanji
						  jamMulaiJanji
						  jamSelesaiJanji
						}
                  }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.JanjibyPasien) return (resolve(resp.data.JanjibyPasien));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
};

export default BuatJanji;
